@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap);
body {
  margin: 0;
  overflow-x: hidden;
  scroll-behavior: smooth;
  font-family: "Source Sans Pro", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #1b1919;
  display: flex;
  color: #ffffff;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
}

body > div {
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgb(0 0 0 / 30%);
  background-color: #656565;
}

::-webkit-scrollbar {
  width: 10px;
  background-color: #f5f5f5;
}

::-webkit-scrollbar-thumb {
  background-color: #2d2d2d;
  height: 200px;
  background-image: -webkit-gradient(
    linear,
    0 0,
    0 100%,
    color-stop(0.5, rgb(45 45 45)),
    color-stop(0.5, transparent),
    to(transparent)
  );
}
.App {
  text-align: center;
}
.page-container {
  display: flex;
  flex-direction: column;
}
.page-bg {
  background: url(/static/media/background_new.22c463e9.png) no-repeat center center fixed;
  background-size: cover;
  display: flex;
  flex-direction: column;
  position: fixed;
  height: -webkit-fill-available;
  width: -webkit-fill-available;
  /* animation: bgAnimate 30s linear infinite alternate; */
}

.page-section {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  /* background-color: #1b1919ad; */
  z-index: 2;
  align-items: center;
}

.limited-showcase-section {
  /* height: 100vh; */
  /* background-color: #1b1919ad; */
  z-index: 2;
  margin-top: 200px;
}

@keyframes bgAnimate {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.2);
  }
}

.section-body {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.section-content {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  padding: 0 24rem;
  height: 100%;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.mint-section-title {
  font-size: 64px;
  margin-bottom: 24px;
  font-weight: 800;
}

.title-center {
  align-self: center;
}

.mint-desc-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 40px;
  margin-bottom: 50px;
  margin-top: 30px;
}

.mint-desc-container {
  display: flex;
  flex-direction: column;
  padding: 22px 40px;
  border-radius: 16px;
  border: 1px solid rgb(128 128 128 / 57%);
  box-shadow: 0 0 0px 2px rgb(128 128 128 / 20%);
}

.mint-desc-container:hover > .mint-desc-heading {
  opacity: 0.5;
}

.mint-desc-container:hover > .mint-desc-value {
  opacity: 0.5;
}

.mint-desc-heading {
  color: #ffffff;
  font-size: 16px;
}

.mint-desc-value {
  color: #ffffff;
  font-size: 42px;
}

.wallet-address {
  color: #ffffff;
  font-size: 28px;
}

.mint-row-center {
  display: flex;
  align-self: center;
  justify-content: center;
}

.connect-wallet-btn {
  border-radius: 20px !important;
  font-size: 18px !important;
  padding: 8px 20px !important;
  background-color: #6f29a5 !important;
  font-family: "Source Sans Pro" !important;
  font-weight: 600 !important;
  color: white !important;
}

.mint-btn {
  border-radius: 7px !important;
  font-size: 18px !important;
  padding: 4px 20px !important;
  background-color: #ffffff !important;
  font-family: "Source Sans Pro" !important;
  font-weight: 600 !important;
  color: #000000ad !important;
  width: 240px;
}

.slider-desc {
  color: #ffffff;
  font-size: 24px;
  margin-bottom: 15px;
}

.mint-slider {
  margin-right: 30px;
  padding-top: 20px;
  width: 100%;
  margin-bottom: 20px;
}

.mint-slider-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.slider-css > .MuiSlider-thumb > .MuiSlider-valueLabel {
  background-color: #ffffff !important;
  color: #26282d !important;
  font-weight: 600 !important;
}

.slider-css > .MuiSlider-markLabel {
  color: rgb(255 255 255 / 78%) !important;
}

.slider-css > .MuiSlider-markLabelActive {
  color: rgb(255 255 255 / 87%) !important;
}

.info-row {
  display: flex;
  align-items: center;
  color: #ffffff;
  margin-top: 20px;
  margin-bottom: 80px;
}

.info-icon {

}

.info-desc {
  font-size: 14px;
  margin-left: 5px;
}

@media (min-width: 320px) and (max-width: 568px) {
  .section-content {
    padding: 0 3rem;
  }

  .mint-section-title {
    font-size: 30px;
    margin-bottom: 16px;
    text-align: center;
  }

  .wallet-connected-title {
    margin-top: 250px;
  }

  .mint-desc-grid {
    grid-template-columns: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 15px;
    margin-bottom: 30px;
  
  }

  .mint-desc-container {
    padding: 15px 25px;
  }

  .mint-desc-heading {
    font-size: 14px;
  }

  .mint-desc-value {
    font-size: 28px;
  }

  .slider-desc {
    font-size: 18px;
    margin-bottom: 15px;
    text-align: center;
  }

  .mint-slider {
    width: 100%;
    margin-bottom: 20px;
    margin-right: 0;
}

  .mint-slider-container {
    flex-direction: column;
  }

  .info-row {
    flex-direction: column;
  }

  .info-desc {
    text-align: center;
  }

  .info-icon {
    margin-bottom: 8px;
  }
}

@media (min-width:410px) and (max-width: 450px) {
  .mint-section-title {
    font-size: 30px;
    margin-bottom: 16px;
    text-align: center;
  }

  .wallet-connected-title {
    margin-top: 350px;
  }

}

@media (min-width: 1025px) and (max-width: 1440px) {
  .section-content {
    padding: 0px 16rem;
  }

  .mint-section-title {
    font-size: 56px;
    margin-bottom: 12px;
  }

  .wallet-connected-title {
    margin-top: 250px;
  }

  .info-row {
    align-items: start;
  }

  .info-desc {
    text-align: center;
  }
}

